@media (max-width: 359px) {
  .sidebar {
    padding-top: 19px;
    padding-bottom: 32px;
    width: 320px;
    height: 100vh;
    font-family: TT Lakes;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 16px 0 52px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.0196078431);
    position: fixed;
    background: white;
    z-index: 50;
  }
  .sidebar_hidden {
    transform: translateX(-100%);
  }
  .sidebar_shown {
    transform: translateX(0);
  }
  .sidebar__header {
    display: flex;
    align-items: center;
  }
  .sidebar__logo {
    margin-left: 17px;
  }
  .sidebar__btn {
    width: 40px;
    height: 40px;
    background-color: #ff3e79;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .btn__line {
    width: 22px;
    height: 2px;
    background-color: white;
    border-radius: 2px;
    position: absolute;
  }
  .sidebar__header .line1 {
    transform: rotate(45deg);
  }
  .sidebar__header .line2 {
    transform: rotate(-45deg);
  }
  .sidebar__header > .btn:nth-child(1) {
    margin-left: 16px;
  }
  .sidebar__header > .btn:nth-child(3) {
    margin-left: 94px;
  }
  .sidebar__menu {
    margin-top: 46px;
    padding: 0;
  }
  .sidebar__item {
    list-style-type: none;
    margin-bottom: 32px;
    padding: 0;
    width: 100%;
    height: 32px;
    display: flex;
    align-items: center;
  }
  .sidebar__item_active {
    color: #7e7e82;
  }
  .sidebar__item_active::before {
    content: "";
    width: 4px;
    height: 32px;
    background-color: #41f6d7;
    position: absolute;
    border-radius: 0 7px 7px 0;
  }
  .sidebar__text {
    margin-left: 16px;
  }
  .sidebar__footer {
    margin-left: 16px;
  }
  .sidebar__buttons {
    display: flex;
    gap: 16px;
  }
  .sidebar__email {
    margin-top: 16px;
    font-size: 16px;
  }
  .sidebar__phone {
    margin-top: 12px;
    font-size: 24px;
    font-weight: 600;
  }
  .sidebar__languages {
    margin-top: 40px;
  }
  .sidebar__language_active {
    color: #7e7e82;
  }
}
